            img.p-2(src="@/assets/curso/temas/tema1/tarjeta4/imagen9.svg", alt='La imagen presenta dos cuadros de dilogo de Atributos de Punto en un software de diseo. Cada cuadro permite configurar el tipo de punto (Curva o Escalado), las coordenadas X e Y, la distancia y el ngulo. Tambin se pueden detallar los valores absolutos y proporcionales del punto previo y del punto siguiente. Los cuadros tienen botones para OK y Cancelar. En el fondo se presenta parte de un grfico con una forma azul claro y puntos de control en rosa.')
        .row(titulo="10")
