          .col-md-7
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta4/imagen10.svg", alt='La imagen presenta una seccin de un software de diseo con el panel de Propiedades abierto a la izquierda. En este panel, se preentan atributos como Profundidad (9.5 cm), Ancho (2 cm), Sobrepuesta (CCW), entre otros. A la derecha, hay un grfico con una forma azul claro y un objeto seleccionado, indicado por un contorno punteado y una figura geomtrica con lneas gua. ')
