          .col-md-7
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta4/imagen11.svg", alt='La imagen presenta el panel Caja herramientas de un software de diseo de patrones a la izquierda, con varias opciones. A la derecha, hay dos piezas de patrn en azul claro etiquetadas como FALDA DELANTERO Base con puntos de control en rosa y lneas gua.')
