            img.p-2(src="@/assets/curso/temas/tema1/tarjeta4/imagen12.svg", alt='La imagen presenta un software de diseo de patrones con el panel Caja herramientas a la izquierda y una pieza de patrn en azul claro a la derecha, etiquetada como FALDA DEL Base. Las opciones del panel incluyen Centrar Aplome (Ctrl+I) y Girar por lnea de hilo (Shift+P).')
        .row(titulo="13")
          .col-md-5.mb-4.mb-md-0
            p.ps-5 13. Al finalizar la parte delantera, d clic derecho sobre la pieza y seleccione limpiar piezas. Reemplace el original, de esta manera se actualiza la pieza en la ventana - ventana de piezas.
          .col-md-7
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta4/imagen13.svg", alt='La imagen expone un men contextual de un software de diseo de patrones junto a una pieza de patrn en azul claro etiquetada como FALDA DELANTERO Base. El men incluye opciones como Seleccionar Herramienta, Atributos, Borrar, Limpiar Piezas con sub opciones Reemplazar Original y Remover Actual, entre otras.')
