    AcordionA(tipo="b" clase-tarjeta="tarjeta tarjeta--purple")(data-aos="zoom-in-left")
      .row.borde-redondo-tmorada(titulo="Paso 1")
        .col-md-4.mb-4.mb-md-0
          p.p-5 Inicie creando un archivo nuevo, sobre el archivo cree un rectngulo con las siguientes medidas:
          ul.lista-ul--color.fa-ul.color-primario.ps-5
              li 
                i.fas.fa-plus
                span.mb-0 #[b Largo:] medida de talle posterior: 42,5 cm.
              li
                i.fas.fa-plus
                span.mb-0 #[b Ancho:]  de busto, 23 cm.
        .col-md-8
          img.p-5(src="@/assets/curso/temas/tema1/tarjeta5/imagen1.svg", alt='La imagen detalla un patrn de costura con varias medidas y etiquetas. En el lado izquierdo, se presenta una lnea vertical con una etiqueta en el centro. A la derecha, se expone un esquema con medidas especficas: Medida de Hombro, Medida de Escote, Cada de Hombro, y Medida Centro Atrs. Tambin se indican instrucciones para ajustar el patrn, como Mitad de la distancia A-D y Bajar Punto B 2 cms. Las letras A, B, C, D, E, F, y H marcan puntos clave en el patrn.')
      .row.borde-redondo-tmorada(titulo="Paso 2")
        .col-md-4.mb-4.mb-md-0
          p.p-5 Haga doble clic sobre el #[b ngulo del escote ] y cambie el atributo del punto, #[b desmarcando escalado y sealando curva. ]
        .col-md-8
          img.p-5(src="@/assets/curso/temas/tema1/tarjeta5/imagen2.svg", alt='La imagen presenta un cuadro de dilogo titulado Atributos de Punto en un software de diseo de patrones. Las opciones permiten seleccionar el tipo de punto, con casillas de verificacin para Escalado y Curva, siendo Curva la opcin seleccionada. Se puede ajustar el ngulo del punto (con el valor 180) y el Nombre del punto. Hay botones para Aplicar, Cancelar, y Costura.')
      .row.borde-redondo-tmorada(titulo="Paso 3")
        .col-md-4.mb-4.mb-md-0
          p.p-5 Los ngulos del hombro y continuacin de la sisa se suprimen. El patrn queda como lo seala la imagen.
        .col-md-8
          img.p-5(src="@/assets/curso/temas/tema1/tarjeta5/imagen3.svg", alt='La imagen presenta tres piezas de patrn de costura alineadas horizontalmente. Cada pieza tiene una lnea central vertical y varias lneas horizontales y curvas en la parte superior que indican diferentes medidas y ajustes. Las piezas parecen representar diferentes etapas de modificacin de un mismo patrn.')
      .row.borde-redondo-tmorada(titulo="Paso 4")
        .col-md-4.mb-4.mb-md-0
          p.p-5 Con la herramienta #[b medir ] se seala el segmento del hombro en el sentido de las manecillas del reloj. Haga clic en el botn #[b editar largo del segmento]; este a su vez abre la ventana largo del segmento.
          p.ps-5 En la ventana editar largo del segmento, escribir 12 cm para el largo y escoja la opcin #[b primer horizontal]. Esta opcin se selecciona porque el punto correspondiente al ngulo del hombro sisa es el que debe moverse al realizar el cambio de medida, y elija primer horizontal porque es el punto inicial.
        .col-md-8
          img.p-5(src="@/assets/curso/temas/tema1/tarjeta5/imagen4.svg", alt='La imagen detalla un patrn de costura a la izquierda con una lnea central vertical y varias lneas de control en los bordes. A la derecha, hay dos cuadros de dilogo. El primero es la Ventana de medidas, que incluye opciones para editar la longitud del segmento, con el botn Editar largo del segmento resaltado. El segundo cuadro de dilogo, titulado Largo del segmento, permite ajustar la longitud de un segmento especfico, con opciones para aceptar, cancelar y copiar/pegar. Elementos como Nuevo, Punto vertical, y Uso horizontal estn resaltados. ')
      .row.borde-redondo-tmorada(titulo="Paso 5")
        .col-md-4.mb-4.mb-md-0
          p.p-5 Ubique el punto mitad de sisa (mitad de la distancia entre el escote y altura de sisa), y marque tambin sobre la lnea donde formar la curva de sisa.
        .col-md-8
          img.p-5(src="@/assets/curso/temas/tema1/tarjeta5/imagen5.svg", alt='La imagen presenta un patrn de costura con una lnea central vertical. Una flecha horizontal seala dos puntos en el patrn, con el texto Medimos la distancia entre estos puntos para deducir la medida de mitad de ancho espalda. Esta instruccin indica cmo determinar la medida de la mitad del ancho de la espalda utilizando los puntos indicados.')
      .row.borde-redondo-tmorada(titulo="Paso 6")
        .col-md-4.mb-4.mb-md-0
          p.p-5 Mida  la distancia sealada en la grfica y a esta, rstele el valor de la mitad del ancho de espalda para saber cunto debe #[b mover el punto ] de la sisa, con el fin de darle la forma a la misma; con la herramienta mover punto, mueva en X el valor que se debe restar para darle la medida de ancho de espalda, y en Y debe quedar 0.
        .col-md-8
          img.p-5(src="@/assets/curso/temas/tema1/tarjeta5/imagen6.svg", alt='La imagen presenta un patrn de costura con una lnea central vertical y una nueva lnea en verde en la parte superior izquierda del patrn. Debajo del patrn, hay un cuadro de dilogo titulado Mover Punto, que permite ajustar la posicin de un punto utilizando coordenadas especficas. Los valores en los campos de entrada son: 3.2 y 3.2 para las coordenadas, y 0 para el ngulo. Hay opciones para aceptar o cancelar la accin.')
      .row.borde-redondo-tmorada(titulo="Paso 7")
        .col-md-4.mb-4.mb-md-0
          p.p-5 #[b Dele la forma ] a la sisa siguiendo las indicaciones de las imgenes.
        .col-md-8
          img.p-5(src="@/assets/curso/temas/tema1/tarjeta5/imagen7.svg", alt='La imagen expone tres etapas en la modificacin de un patrn de costura. Primero, se presenta el patrn original. Luego, se aade una lnea verde curva al patrn, utilizando un cuadro de dilogo titulado Crear arco con campos para el nmero de puntos y el radio. Finalmente, el patrn modificado detalla la curva aplicada, indicando que el punto seleccionado tiene atributos tanto de escalado como de curva, segn el texto que dice: Doble clic sobre este punto y adems de atributo de escalado, le damos tambin de curva. ')
      .row.borde-redondo-tmorada(titulo="Paso 8")
        .col-md-4.mb-4.mb-md-0
          p.p-5 Con la herramienta mover punto, entalle la medida de cintura correspondiente a la talla. Fije un punto con atributo de escalado a 9 cm, que corresponde a la medida de la mitad de separacin del busto. Segn el mtodo de patronaje utilizado, el costado posterior es igual a la medida A-B ms 1 cm. Se edita el largo del segmento de costado, siguiendo las indicaciones de la imagen.
        .col-md-8
          img.p-5(src="@/assets/curso/temas/tema1/tarjeta5/imagen8.svg", alt='La imagen expone dos patrones de costura y dos cuadros de dilogo. El primer patrn est etiquetado con puntos A y B, indicando que A ponemos punto a mitad de separacin de hombro. El segundo patrn refleja una modificacin con una curva aadida. Los cuadros de dilogo muestran la Ventana de medidas con opciones para editar la longitud del segmento y el cuadro Largo del segmento con opciones para ajustar la longitud y posicin del segmento, resaltando Largo y Primera posicin. ')

