        .row.justify-content-center.my-lg-4
          .col-md-6.mb-4.mb-md-0
            p.p-4 #[b 1. ] Tome la herramienta pinza y reparta a cada lado del punto sealado, mitad de la amplitud de la pinza, para este caso, 1,5 a cada lado del punto. Traiga una lnea gua a la altura de la sisa hasta donde llega la profundidad de sisa para marcar hasta ah el largo de esta. Se marca la pinza, llevando la misma hasta la lnea de altura de la sisa y en la ltima ventana #[b atributos de pinza ] se activa la opcin #[b sobrepuesta].
          .col-md-6
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta6/imagen1.png", alt='La imagen detalla un programa de diseo de patrones de costura. En la parte superior, se expone una ventana de atributos de pieza con opciones para seleccionar, desfilar y cambiar medidas de la pieza. La opcin Subgrupo est destacada con un crculo rojo. En la parte inferior, hay dos diagramas de patrones de una prenda. El diagrama de la izquierda describe una lnea verde, que parece indicar una modificacin en el patrn original. El diagrama de la derecha expone el mismo patrn con lneas punteadas azules, indicando las lneas de costura y cortes.')
        .row.justify-content-center.my-lg-4
          .col-md-6.mb-4.mb-md-0
            p.p-4 #[b 2. ] La pinza debe llegar al punto de interseccin entre altura y separacin de busto. Para ello utilice la herramienta pinza, seale el punto del vrtice de la #[b pinza], arrastre y seleccione el punto de interseccin entre las lneas guas, haga clic y el vrtice de la pinza se traslada al punto sealado.
          .col-md-6
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta6/imagen2.png", alt='La imagen expone dos diagramas de patrones de costura. Ambos diagramas estn enfocados en una misma seccin de los patrones, que est resaltada con un crculo rojo. La seccin resaltada indica un ajuste o una modificacin especfica en la lnea de costura central. El patrn de la izquierda y el de la derecha son similares, pero el detalle en el crculo rojo puede sealar un punto de ajuste clave en el diseo del patrn, probablemente relacionado con la colocacin de la pinza o un punto de interseccin de lneas importantes.')
        .row.justify-content-center.my-lg-4
          .col-md-6.mb-4.mb-md-0
            p.p-4 #[b 3. ] Para terminar el patrn, haga clic en la herramienta #[b reemplazar original ] (grupo de herramientas generales), de esta manera en la #[b barra de piezas] quedan guardados los cambios que se hicieron al rectngulo desde el inicio. Para guardar el archivo, dirjase a la barra de #[b men archivo], opcin #[b guardar como], proceder a darle un nombre al archivo, este queda con #[b extensin dsn].
          .col-md-6
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta6/imagen3.png", alt='La imagen detalla dos diagramas de patrones de costura para el cuerpo de una prenda. El diagrama de la izquierda presenta un patrn bsico con lneas punteadas que indican la ubicacin de una pinza en la cintura, y el contorno del patrn est delineado con lneas slidas negras. El diagrama de la derecha expone el mismo patrn pero con el rea sombreada en azul, destacando la pieza completa. Ambas imgenes tienen las mismas lneas punteadas y slidas para indicar las caractersticas del patrn, siendo la principal diferencia el sombreado azul en el diagrama derecho. ')

