    .tarjeta.tarjeta--gris.p-4.borde-redondo(style="background-color: #F8F5FF")(data-aos="zoom-in-left")
      LineaTiempoC.color-acento-botones(text-small)
        .row(titulo="1")
          .col-md-5.mb-4.mb-md-0
            p.ps-5.mb-4 #[b 1. ] Se parte de un rectngulo, cuyas dimensiones son:
            ul.lista-ul--color.fa-ul.color-primario.ps-5
              li 
                i.fas.fa-plus
                span.mb-0 #[b Largo del rectngulo=] medida de talle delantero.
              li
                i.fas.fa-plus
                span.mb-0 #[b Ancho=]  de la medida de contorno de busto.
            p.ps-5.mt-4.mb-4 Sobre el rectngulo se ubican los puntos de:
            ul.lista-ul--color.fa-ul.color-primario.ps-5
              li 
                i.fas.fa-plus
                span.mb-0 #[b Centro frente (punto A).]
              li
                i.fas.fa-plus
                span.mb-0 #[b  de la medida de talle (punto B).] 
              li
                i.fas.fa-plus  
                span.mb-0 #[b Este punto se ubica utilizando la ventana de punto proporcional 0,5.]
              li
                i.fas.fa-plus
                span.mb-0 #[b El punto B corresponde a la altura de sisa.]
          .col-md-7
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta7/imagen1.svg", alt='La imagen presenta cmo ubicar un punto en un patrn de costura usando un software de diseo. Se expone un rectngulo con dos puntos, Punto A y Punto B, conectados por una lnea horizontal. A la derecha, una ventana de Atributos de Punto detalla las configuraciones, destacando la opcin Proporcional en 0.5. Esta configuracin posiciona el punto de manera proporcional respecto a otros puntos en el patrn.')
        .row(titulo="2")
          .col-md-5.mb-4.mb-md-0
            p.ps-5 #[b 2. ] Trace lneas guas sobre la cada y la medida de hombro. Eleve el ngulo superior derecho del rectngulo a la interseccin de las lneas guas. Para ello, se utiliza la herramienta #[b mover punto]. Haga clic sobre el punto del ngulo superior izquierdo del rectngulo, cambiar el atributo de escalado a curva y modele la curva de escote con la herramienta #[b mover punto].
          .col-md-7
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta7/imagen2.svg", alt='La imagen expone el diseo de un patrn de costura enfocado en la medida del escote y del hombro. A la izquierda, se indican las medidas del escote y del hombro con flechas rojas en la parte superior del rectngulo, junto con una flecha adicional que seala la Cada de Hombro. A la derecha, se presenta el patrn ajustado con una lnea curva que representa la cada del hombro, y lneas punteadas azules que indican las guas para las medidas del escote y el hombro. Este diagrama explican cmo tomar y aplicar estas medidas en el patrn de una prenda.')
        .row(titulo="3")
          .col-md-5.mb-4.mb-md-0
            p.ps-5 #[b 3. ] Trace la curva de escote utilizando la herramienta #[b punto en contorno]. Suprima los puntos de la imagen anterior para definir la lnea de hombro y lnea de sisa. Se puede tomar como referencia las #[b lneas guas].
          .col-md-7
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta7/imagen3.svg", alt='La imagen presenta un patrn de costura con lneas y puntos que indican las medidas y ajustes para el escote y el hombro. Las lneas punteadas azules representan guas de referencia para estas medidas. La lnea curva en la parte superior izquierda indica el escote, mientras que las lneas inclinadas y rectas a la derecha muestran la cada y la posicin del hombro. Este patrn expone cmo aplicar las medidas del escote y el hombro en el diseo de una prenda. ')
        .row(titulo="4")
          .col-md-5.mb-4.mb-md-0
            p.ps-5 #[b 4. ] Seleccione la lnea de hombro en #[b sentido de las manecillas del reloj]. Haga clic en el botn #[b editar largo del segmento]; en esta ventana se escribe #[b el valor a editar] que para la talla 10 corresponde a 12 cm, tambin seleccione la opcin #[b ltima horizontal], segn la posicin de la grfica. Al escoger esta opcin se debe tener en cuenta que el punto a mover para ajustar la medida debe ser el #[b punto hombro-sisa. ]
          .col-md-7
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta7/imagen4.svg", alt='La imagen expone un software de diseo de patrones de costura, enfocado en la configuracin del largo de un segmento. En la parte superior izquierda, hay una ventana titulada Largo del segmento donde se introduce la medida de 12 en el campo Largo, resaltada en un crculo rojo. Abajo, otra ventana titulada Editar largo del segmento tambin tiene el campo Largo con el valor 12.05, igualmente resaltado. A la derecha, el patrn de costura presenta las modificaciones aplicadas, con lneas y guas punteadas azules que ayudan a ajustar y verificar las dimensiones del segmento en el diseo de la prenda. ')
        .row(titulo="5")
          .col-md-5.mb-4.mb-md-0
            p.ps-5 #[b 5. ] Para editar la medida de hombro, utilice la herramienta #[b medida].
          .col-md-7
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta7/imagen5.svg", alt='La imagen detalla una barra de herramientas del software de diseo de patrones de costura, especficamente la seccin llamada Pieza. Entre los conos disponibles, uno est resaltado con un crculo rojo, aunque parece estar deshabilitado (en gris). Los conos incluyen herramientas para manipular y ajustar piezas del patrn, como medir, cortar, y seleccionar diferentes tipos de prendas.')
        .row(titulo="6")
          .col-md-5.mb-4.mb-md-0
            p.ps-5 #[b 6. ] Ubique el punto de #[b altura de pecho]. Para ello, se utiliza la herramienta #[b punto en contorno ] y la #[b opcin proporcional 0,5]. Seleccione el atributo de escalado. La ubicacin de este punto se marca tambin sobre la lnea de sisa.
          .col-md-7
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta7/imagen6.svg", alt='La imagen detalla una ventana de Atributos de Punto del software de diseo de patrones de costura junto a un patrn de prenda. En la ventana, se pueden configurar los atributos del punto, como el tipo de punto (Curva o Escalado), coordenadas X e Y, distancia, ngulo, y la opcin de mover sobre el contorno. Tambin se destacan los valores de posicin absoluta y proporcional del punto. A la derecha, el patrn presenta el punto ajustado en el diseo de la prenda, con lneas y puntos que indican las medidas y ajustes especficos. ')
        .row(titulo="7")
          .col-md-5.mb-4.mb-md-0
            p.ps-5 #[b 7. ] Con la herramienta #[b mover punto]; corra el punto a la distancia que resulte de la resta de la medida que dio entre los dos puntos y la medida de mitad de ancho de pecho, segn la talla que se est trazando.
          .col-md-7
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta7/imagen7.svg", alt='La imagen presenta dos diagramas de patrones de costura. En el diagrama superior, se indican dos puntos especficos con crculos rojos, entre los cuales se mide la distancia utilizando la herramienta Medida. El texto entre los puntos dice: Medimos la distancia entre estos puntos, con la herramienta Medida. En el diagrama inferior, se presenta un patrn de prenda con las medidas y ajustes aplicados, reflejando la modificacin basada en la distancia medida entre los dos puntos en el diagrama superior. ')
        .row(titulo="8")
          .col-md-5.mb-4.mb-md-0
            p.ps-5 #[b 8. ] Trace la curva de la sisa e identifique el punto de vrtice de pinza.
          .col-md-7
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta7/imagen8.svg", alt='La imagen presnta un patrn de costura con instrucciones para trazar la curva de la sisa y ubicar el punto de separacin del busto. En la parte superior, se indica Trazar la curva de sisa con ayuda de lneas guas, utilizando lneas punteadas azules como referencia. En la parte inferior, una flecha roja seala Ubicar el punto de separacin de busto, marcando su posicin en el patrn.')
        .row(titulo="9")
          .col-md-5.mb-4.mb-md-0
            p.ps-5 #[b 9. ] Para editar el largo de medida de costado, seleccione, los puntos en el #[b sentido de las manecillas del reloj]. De acuerdo con la figura se debe editar el ltimo punto en forma diagonal. La medida de costado del patrn delantero #[b debe ser igual al costado del posterior. ]
          .col-md-7
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta7/imagen9.svg", alt='La imagen presenta una ventana de "Largo del segmento" del software de diseo de patrones de costura. A la izquierda, la "Ventana de medidas"  detalla como la distancia, ngulo, y largo del segmento seleccionado, con un valor base de 22.25. En el centro, se ve el patrn de una prenda con lneas que indican las medidas ajustadas. A la derecha, otra ventana de "Largo del segmento" presenta un valor de 18.25 y opciones para extender el segmento en direccin vertical, horizontal o diagonal. Estas ventanas permiten ajustar y medir segmentos especficos del patrn.')
        .row(titulo="10")
          .col-md-5.mb-4.mb-md-0
            p.ps-5 #[b 10. ] El patrn debe quedar segn la ilustracin.
          .col-md-7
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta7/imagen10.svg", alt='La imagen expone una ventana emergente de un software de diseo de patrones de costura. La ventana emergente est titulada ventana piezas y ofrece opciones para editar una pieza del patrn. El men desplegable presenta las opciones Editar, Pieza, y Borrar. Bajo la opcin Pieza, se pueden elegir las subopciones Reemplazar Original, Remover Actual y Crear como nueva. En el fondo, se identitica parte de un patrn de costura y una regla de medicin, lo que sugiere que el usuario est trabajando en ajustar o modificar el diseo del patrn. ')
        .row(titulo="11")
          .col-md-5.mb-4.mb-md-0
            p.ps-5 #[b 11. ] Corregir el sentido de hilo de la tela con la herramienta #[b hilo paralelo al tramo.] Centre y aplome con la herramienta #[b nueva lnea base.] Por ltimo, actualizar los cambios en la ventana de piezas, con la opcin #[b reemplazar original.]
          .col-md-7
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta7/imagen11.svg", alt='La imagen presenta un patrn de costura para la parte delantera de una prenda, como una camisa o blusa. Incluye las lneas de contorno para los bordes, puntos de referencia importantes (marcados en rosa y negro), y lneas de construccin para guiar el corte y la costura. La Base Delantero es una lnea vertical utilizada como referencia. Las curvas en la parte superior representan el escote y la sisa, esenciales para el ajuste de la prenda. El patrn gua el corte y ensamblaje de la tela para asegurar un ajuste correcto y la forma deseada. ')

