    .tarjeta.tarjeta--azul.p-4.mb-4.borde-redondo(style="background-color: #F8F5FF")(data-aos="zoom-in-left")
      SlyderA(tipo="b")
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 1. ] Para dar inicio se parte de la base superior delantera, trace una lnea gua hasta la lnea de cintura o largo de talle delantero.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta8/imagen1.svg", alt='La imagen detalla dos patrones de costura para la parte delantera y trasera de una prenda, como una camisa o blusa. El patrn de la izquierda, marcado como Base SUPERIOR FRENTE, corresponde a la parte delantera, mientras que el de la derecha, Base SUPERIOR ESPALDA, corresponde a la parte trasera. Ambos patrones incluyen puntos de referencia en rosa que indican lugares clave para la construccin de la prenda. Las lneas de contorno definen los bordes de cada pieza, incluyendo el escote, la sisa, el costado y el dobladillo. Las flechas indican la direccin de la base y sirven como gua para la alineacin y corte de la tela.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 2. ] Traiga  una lnea gua de la regla vertical y llvela hasta el ngulo #[b cintura costado.] Con la herramienta #[b mover punto,] lleve el ngulo cintura costado a la interseccin de las dos lneas guas.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta8/imagen2.svg", alt='La imagen detalla dos patrones de costura para la parte delantera de una prenda, etiquetados como Base SUPERIOR FRENTE. Ambos patrones son azules y tienen puntos de referencia en rosa para la construccin. Las lneas de contorno definen los bordes de la pieza, y las flechas indican la direccin de la base para guiar el corte de la tela.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 3. ] Valide que la lnea de cintura est #[b totalmente recta] sobre la lnea de talle delantero, y verifique con la #[b tecla F8] qu medida tiene la cintura.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta8/imagen3.svg", alt='La imagen presenta dos patrones de costura: uno para la parte delantera (izquierda) y otro para la parte trasera (derecha) de una prenda. El patrn delantero est marcado como Base SUPERIOR FRENTE y est coloreado en azul, mientras que el trasero est marcado como Base SUPERIOR ESPALDA. Ambos patrones incluyen puntos de referencia en rosa y medidas especficas en cada punto, indicando las distancias entre los diferentes puntos clave del patrn. Las flechas indican la direccin de la base para guiar el corte de la tela. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.ps-5.mt-4 #[b 4. ] En la lnea de cintura se identifica un punto con atributo de escalado a 9 cm, este punto determina la medida de separacin del busto. #[b Medida de cintura:]
            ul.lista-ul--color.fa-ul.color-primario.ps-5
              li 
                i.fas.fa-plus
                span.mb-0 #[b Cintura:]  contorno de cintura + pinza.
            p.ps-5 Al tomar como punto de partida los 9 cm, marque un punto a 12 cm para que quede exactamente la medida  contorno de cintura, 17 cm + 4 cm de pinza, para un total de 21 cm. Con la herramienta #[b puntos sobre ] (tecla rpida O) a partir de los 9 cm, ubicar un punto a 12 cm o el restante para la medida de cintura.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta8/imagen4.svg", alt='La imagen expone una ventana de configuracin de atributos de punto en un software de diseo de patrones de costura, junto a un patrn para la parte delantera de una prenda etiquetado como Base SUPERIOR FRENTE. En la ventana de configuracin, se pueden ajustar las coordenadas (X e Y), la distancia, el ngulo, y si el punto es una curva o escalado. Tambin se detalla la posicin relativa y absoluta del punto en relacin con otros puntos del patrn.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 5. ] D clic derecho, escoja la herramienta #[b seleccin], seale el punto del costado y suprimir. Para verificar nuevamente las medidas, oprimir la #[b tecla F8].
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta8/imagen5.svg", alt='La imagen presenta tres vistas de patrones de costura en un software de diseo. En la vista izquierda, se detalla el patrn de la parte delantera de una prenda etiquetado como Base SUPERIOR FRENTE. En el centro, hay un men contextual del software con opciones como Seleccionar Herramienta, Lneas gua, Nueva pieza, Insertar, Herramientas, Segmento, Zoom, y Deshacer Agregar un punto. En la parte derecha, se expone el mismo patrn delantero con medidas especficas entre puntos clave. Los patrones estn en azul y tienen puntos de referencia en rosa, con lneas de contorno que definen los bordes y flechas que indican la direccin de la base.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 6. ] Si la medida de los costados de la pieza del frente y del posterior no coinciden, trace una pinza correctiva de talle. Con la herramienta #[b punto sobre], ubicar un punto a 5 cm por debajo de la altura de la sisa.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta8/imagen6.svg", alt='La imagen detalla una ventana de configuracin de atributos de punto en un software de diseo de patrones de costura, junto a un patrn para la parte delantera de una prenda etiquetado como Base SUPERIOR FRENTE. La ventana permite ajustar las coordenadas X e Y, la distancia, y el ngulo de un punto especfico en el patrn. Tambin se puede seleccionar si el punto es una curva o escalado, y moverlo sobre el contorno. Adems, se presentan las posiciones absoluta y proporcional del punto en relacin con otros puntos del patrn. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 7. ] Marque una lnea gua, exactamente al #[b punto ] de los 5 cm. Con la herramienta punto, fijar ese mismo punto sobre la lnea centro frente. Marque una lnea gua vertical para identificar el #[b vrtice de pinza.]
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta8/imagen7.svg", alt='La imagen detallan dos versiones del patrn de costura para la parte delantera de una prenda, etiquetados como Base SUPERIOR FRENTE. Ambos patrones son azules y tienen puntos de referencia en rosa. Las lneas de contorno definen los bordes de cada pieza, y las flechas indican la direccin de la base. La diferencia entre los dos patrones es que el de la derecha tiene una lnea adicional horizontal y vertical, posiblemente para ajustes o modificaciones especficas del diseo. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 8. ] Para verificar el largo de los costados, oprima la #[b tecla F8]. Para validar el largo del costado delantero, tome la medida con la herramienta #[b medir], del grupo de herramientas de pieza, o sume los dos segmentos que aparecen.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta8/imagen8.svg", alt='La imagen expone dos patrones de costura, uno delantero y otro trasero, ambos etiquetados como Base SUPERIOR FRENTE y Base SUPERIOR ESPALDA. Cada patrn est en azul con puntos de referencia en rosa y medidas entre puntos clave. Tambin se ve una herramienta de medicin del software de diseo, que permite ajustar ngulos y longitudes de los segmentos del patrn.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 9. ] Para poder deducir la #[b medida de la pinza], en el grupo de herramientas #[b vista], opcin #[b otras herramientas], seleccione #[b calculadora. ]
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta8/imagen9.svg", alt='La imagen presenta una interfaz de software de diseo de patrones de costura con un men desplegable que ofrece varias opciones como calcular, mostrar lneas gua y otras herramientas. A la derecha, se expone una calculadora. Estos elementos indican la integracin de herramientas de medicin y clculo dentro del software para ajustar y disear los patrones de costura.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 10. ] Despus de tener clara la medida de la pinza, #[b marque pinza]. Tome la herramienta #[b pinza], opcin #[b insertar pinza], desde el punto de los 5 cm de altura de busto, dar la medida correspondiente a la amplitud de la pinza, marque la #[b profundidad o el largo ] de pinza, hasta la interseccin de las lneas guas, correspondiente al vrtice de pinza o altura de busto.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta8/imagen10.svg", alt='La imagen presenta dos capturas de pantalla de un software de diseo de patrones. La izquierda presenta una ventana para ajustar atributos de punto (coordenadas, distancia y ngulo), mientras que la derecha expone el patrn delantero de una prenda, etiquetado como Base SUPERIOR FRENTE, con un punto ajustado. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 11. ] Con la pinza correctiva de talle se realizarn todos los traslados de pinza. Para crear la pinza de entalle o ajuste por cintura, primero #[b elimine lneas guas y limpie el rea de trabajo]. Tome la herramienta pinza, opcin #[b insertar pinza] y asigne la medida para el primer y segundo lado de pinza. La pinza de entalle debe quedar entre 2 a 3 cm por debajo de la pinza de recuperacin de talle.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta8/imagen11.svg", alt='La imagen detalla tres capturas de pantalla de un software de diseo de patrones de costura. La primera captura presenta una ventana de configuracin de atributos de punto, permitiendo ajustar coordenadas, distancia, y ngulo de un punto. La segunda captura expone el patrn delantero de una prenda con un punto ajustado y lneas adicionales. La tercera captura tambin detalla el patrn delantero con modificaciones en las lneas de construccin.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 12. ] Para realizar el traslado de pinza se sugiere que mueva el hilo de tela, con el fin de no interferir con el traslado seleccione la herramienta #[b mover interno] (tecla rpida I).
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta8/imagen12.svg", alt='La imagen expone tres capturas de pantalla de un software de diseo de patrones de costura. La primera captura presenta la Caja de herramientas con varias opciones para mover puntos y piezas, resaltando Mover Interno (I). La segunda y tercera capturas detallan el patrn delantero de una prenda, etiquetado como Base SUPERIOR FRENTE, con lneas y puntos ajustados utilizando las herramientas seleccionadas.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 13. ] Como ya se identifican las pinzas y para poder actualizarlas pulse #[b clic derecho, opcin, limpiar pieza, actualizar pieza. Eliminar o suprimir la pieza posterior.]
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta8/imagen13.svg", alt='La imagen expone tres capturas de pantalla de un software de diseo de patrones de costura. La primera captura presenta la Caja de herramientas con varias opciones para mover puntos y piezas, resaltando Mover Interno (I). La segunda y tercera capturas detallan el patrn delantero de una prenda, etiquetado como Base SUPERIOR FRENTE, con lneas y puntos ajustados utilizando las herramientas seleccionadas.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 14. ] Para poder realizar los diferentes tipos de traslados de pinza, copie la pieza actual del delantero. Para copiar la pieza, en la ventana pieza d clic #[b derecho en pieza - crear como nueva]. Cree las copias que considere necesarias de acuerdo con la cantidad de traslados de pinza que se vayan a realizar.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta8/imagen14.svg", alt='La imagen expone una captura de pantalla de un software de diseo de patrones de costura con una ventana emergente de opciones para editar piezas. La opcin seleccionada es Crear copia nueva. En el fondo, hay varios patrones delanteros de una prenda etiquetados como Base SUPERIOR FRENTE, uno de ellos en azul y los dems en blanco. Cada patrn tiene lneas y puntos de referencia ajustados. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 15. ] Para empezar los traslados de pinza inicie con un traslado de hombro, tome #[b herramienta pinza], opcin #[b aadir y girar pinza]. Al acercarse al vrtice de pinza, de la pinza correctiva de talle, se identifica un cambio de color, sealando qu se puede modificar o trasladar.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta8/imagen15.svg", alt='La imagen expone un men del software de diseo de patrones con la opcin Aadir y girar pinza seleccionada. A la derecha, un patrn delantero de una prenda (Base SUPERIOR FRENTE) tiene una pinza aadida y ajustada.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 16. ] En el sentido de las manecillas del reloj se traslada la pinza hacia el hombro para que la pinza quede en toda la mitad de la medida o el largo de hombro; en la ventana de dilogo #[b casilla proporcional] asigne 0.5. Al validar la medida se identifica que el recorrido del patrn se torna de un color diferente desde un punto hacia el otro, esto quiere decir que ya est lista la pieza para realizar el traslado.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta8/imagen16.svg", alt='La imagen detalla dos capturas de pantalla de un software de diseo de patrones de costura. A la izquierda, se presenta una ventana de configuracin de atributos de punto con opciones para ajustar coordenadas, distancia y ngulo. A la derecha, se expone un patrn delantero de prenda etiquetado como Base SUPERIOR FRENTE con ajustes  en las lneas de construccin y una pinza aadida. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 17. ] Haga clic sobre cualquier punto de la pieza, automticamente la pinza se ancla y al cerrar o mover la pieza se identifica el traslado de la pinza hacia el lado deseado.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta8/imagen17.svg", alt='La imagen presenta dos patrones delanteros de una prenda etiquetados como Base SUPERIOR FRENTE. A la izquierda, el patrn tiene lneas verdes adicionales que indican ajustes o aadidos, como una pinza. A la derecha, el patrn exponen ms modificaciones con una pinza aadida y un rea destacada con un recuadro punteado, indicando un ajuste especfico.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 18. ] De esa manera se deben hacer los traslados de pinza a cada una de las partes del cuerpo.
