        h3.mb-0.ms-2 Cmo convertir los traslados de pinza a cortes?

    .tarjeta.tarjeta--gris.p-4.borde-redondo(style="background-color: #F8F5FF")(data-aos="zoom-in-left")
      LineaTiempoC.color-acento-botones(text-small)
        .row(titulo="1")
          .col-md-4.mb-4.mb-md-0
            p.ps-5.mb-4 #[b 1. ] Tomar herramienta #[b pinza], opcin #[b aadir y girar pinza]. Encontrar las pinzas en el punto central del busto.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta9/imagen1.svg", alt='La imagen detalla tres capturas de pantalla de un software de diseo de patrones de costura. A la izquierda, se presenta un men desplegable con la opcin Aadir y girar pinza seleccionada. En el centro y a la derecha, se exponen patrones delanteros de una prenda, etiquetados como Base SUPERIOR FRENTE, con pinzas aadidas y ajustadas, representadas por lneas de construccin y puntos de referencia en el rea del busto.')
        .row(titulo="2")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 2. ] Grupo de herramientas #[b piezas], opcin #[b cortar a pieza]. El cursor cambia a una estrella y tijeras, la estrella debe coincidir sobre el punto que se quiere cortar.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta9/imagen2.svg", alt='La imagen detalla una captura de pantalla de un software de diseo de patrones de costura, enfocada en la herramienta Pieza. El men desplegable ofrece dos opciones: Cortar a pieza y Cortar a pieza a lo largo de un interno. La opcin Cortar a pieza est seleccionada. Esta herramienta permite ajustar y modificar las piezas del patrn dentro del software.')
        .row(titulo="3")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 3. ] Inicie por el punto de hombro hacia centro frente, haga clic en el #[b vrtice de pinza], y luego d clic en el botn #[b aceptar] de la ventana emergente.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta9/imagen3.svg", alt='La imagen detalla dos capturas de pantalla de un software de diseo de patrones de costura. En la captura de la izquierda, se presenta un patrn delantero de prenda etiquetado como Base SUPERIOR FRENTE con lneas de construccin y puntos de referencia. A la derecha, se presenta la ventana Mover Puntos, que permite ajustar la posicin de un punto especfico en el patrn, con opciones para mover desde un punto, un segmento, o una ventana definida, y para ajustar las coordenadas y ngulos del punto seleccionado.')
        .row(titulo="4")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 4. ] Seguir cortando hasta el punto de cintura centro frente, al finalizar el corte se habilita una ventana emergente para asignar margen de costura, en este caso se est trabajando sin margen de costura, as que presione el #[b botn cancelar.]
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta9/imagen4.svg", alt='La imagen presenta dos capturas de pantalla de un software de diseo de patrones de costura. A la izquierda, se expone un patrn delantero de prenda etiquetado como Base SUPERIOR FRENTE con lneas de construccin y puntos de referencia. A la derecha, se detalla la ventana Atributos de Costura, que permite definir el inicio y fin de las costuras, las costuras de esquina, y ajustar el ancho de costura. Tambin incluye opciones para conservar escalado y aplicar configuraciones especficas a las costuras del patrn.')
        .row(titulo="5")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 5. ] Al generar el corte se crea un hilo de tela diferente, es decir, quedan hilos de tela independientes por pieza. Al seleccionar la pieza y al arrastrar se identifica la divisin o el corte.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta9/imagen5.svg", alt='La imagen detalla dos patrones de costura para la parte delantera de una prenda, etiquetados como Base SUPERIOR FRENTE. Cada patrn presenta variaciones en las lneas de construccin y puntos de referencia, indicando diferentes ajustes y modificaciones. Los patrones estn en azul con puntos de referencia en rosa, y las lneas de construccin exponen cmo se pueden alterar los patrones para adaptar la prenda.')
        .row(titulo="6")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 6. ] Para eliminar los excedentes de pinza del corte siguiente se debe tomar la herramienta, #[b cortar a pieza] (tecla rpida C), fijar punto desde el hombro hacia sisa, hasta punto de vrtice de pinza, #[b cancelar ventana emergente y separar pieza.]
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta9/imagen6.svg", alt='La imagen detalla tres capturas de pantalla de un software de diseo de patrones de costura. A la izquierda, se presenta el men de la herramienta Pieza con la opcin Cortar a pieza seleccionada. En el centro, se expone un patrn delantero de una prenda etiquetado como Base SUPERIOR FRENTE con lneas de construccin adicionales que indican un corte. A la derecha, el patrn cortado en dos piezas separadas, reflejando el ajuste realizado.')
        .row(titulo="7")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 7. ] Tome nuevamente herramienta cortar #[b pieza] (tecla rpida C), y finalizar el corte desde vrtice de pinza hasta cintura.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta9/imagen7.svg", alt='La imagen detalla dos capturas de pantalla de un software de diseo de patrones de costura. A la izquierda, se presenta un patrn delantero de prenda etiquetado como Base SUPERIOR FRENTE cortado en dos partes. A la derecha, el patrn se ha dividido en tres piezas separadas: la parte superior, una pieza intermedia, y una pieza pequea adicional, todas etiquetadas y ajustadas segn las lneas de construccin y puntos de referencia.')
        .row(titulo="8")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 8. ] Separe la pieza hasta obtener piezas entre frente, pieza acostado y sobrantes de pinza. Para eliminar los sobrantes de las pinzas, haga #[b clic sobre la pieza] para identificar el nombre o la pieza en la #[b ventana de piezas]. Luego de identificar la pieza en la ventana de piezas, d clic sobre la ventana, opcin #[b editar - borrar pieza desde modelo.]
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta9/imagen8.svg", alt='La imagen presenta dos capturas de pantalla de un software de diseo de patrones de costura. En la captura de la izquierda, se presenta la ventana Ventana piezas con varias piezas de patrones, destacando la Base SUPERIOR FRENTE que se ha dividido en varias partes. En la captura de la derecha, un men contextual indica las opciones para editar, cortar, copiar y borrar piezas, con la opcin Borrar Pieza desde modelo resaltada. Las piezas del patrn se presentan separadas y etiquetadas, listas para ser ajustadas o eliminadas segn sea necesario.')
        .row(titulo="9")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 9. ] Repita el ejercicio hasta eliminar todos los sobrantes y dejar nicamente las piezas correspondientes al corte. Con el fin de suavizar las puntas generadas en cada uno de los cortes, lo primero que debe hacer es limpiar la pieza, para ello, #[b haga clic sobre el corte], una vez se seleccione indica, que qued el segmento de la pinza anterior, oprimir la tecla #[b suprimir] para eliminar dicho segmento.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta9/imagen9.svg", alt='La imagen expone tres capturas de pantalla de un software de diseo de patrones de costura. En la primera captura, se detalla la ventana "Ventana piezas" con varias piezas de patrones, destacando la "Base SUPERIOR FRENTE" que se ha dividido en varias partes. En la segunda captura, se presenta un patrn delantero de prenda con una pinza aadida y lneas de construccin adicionales, representado como varias piezas etiquetadas. En la tercera captura, las piezas del patrn se exponen separadas y etiquetadas, destacando la "Base SUPERIOR FRENTE" dividida en tres partes, listas para ser ajustadas o ensambladas.')
        .row(titulo="10")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 10. ] Repita el ejercicio hasta que no se identifiquen segmentos externos a cada una de las piezas. Al tener la pieza completamente limpia de cualquier segmento, d clic sobre el punto de vrtice o de interseccin de pinza y en la pestaa #[b propiedades], opcin #[b principal], habilite el #[b atributo curva.]
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta9/imagen10.svg", alt='La imagen presenta una interfaz de software de diseo de patrones de costura. En la parte superior, hay una barra con iconos de diferentes piezas de patrones. En el centro, se exponen dos piezas de patrn: una recta y otra curva, con lneas y puntos de ajuste. A la izquierda, hay un panel de propiedades con opciones seleccionables como Punto, Escalada, Curva, entre otras. ')
        .row(titulo="11")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 11. ] Al asignar el #[b atributo de curva] se modifica toda la pieza.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta9/imagen11.svg", alt='La imagen presenta una interfaz de software de diseo de patrones de costura. En la parte superior, hay una barra con iconos de diferentes piezas de patrn etiquetadas como Base Sup. En el centro, se exponen dos piezas de patrn: una recta y otra curva (coloreada en azul), con lneas y puntos de ajuste. A la izquierda, hay un panel de propiedades con opciones como Principal, Escalado, Curva, Conectado, y otras.')
        .row(titulo="12")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 12. ] Para evitar modificar todo el trayecto se sugiere agregar punto en ambas direcciones y colocar la medida del trayecto dependiendo del volumen del busto o copa, sin seleccionar ningn atributo.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta9/imagen12.svg", alt='La imagen representa dos capturas de pantalla de una interfaz de software de diseo de patrones de costura. En ambas capturas, se presentan dos piezas de patrn: una recta y otra curva (coloreada en azul). Cada captura tiene una ventana emergente titulada Atributos de Punto, donde se pueden ajustar valores como la distancia y el ngulo. Ambas ventanas tienen opciones para Cerrar, Mostrar valor constante, y campos para ingresar detalles especficos de los puntos. ')
        .row(titulo="13")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 13. ] Al seleccionar el #[b punto de vrtice pinza ] y pasar atributo de curva se identifica cmo se suaviza el vrtice. Seleccionar herramienta #[b mover punto y suavizar] como se hara de manera manual.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta9/imagen13.svg", alt='La imagen expone dos partes de una interfaz de software de diseo de patrones de costura. La parte izquierda detalla la interfaz completa con dos piezas de patrn, una recta y otra curva (coloreada en azul), y un panel de propiedades con opciones como Principal, Escalado, Curva, entre otras. La parte derecha representa una ampliacin de la pieza curva en azul, detallando los puntos y lneas de ajuste. ')
        .row(titulo="14")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 14. ] Para modificar o actualizar el nombre de la pieza, seleccionar la pieza en la ventana, propiedades, pestaa cambiar nombre, haga doble clic y escribir el nombre deseado.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta9/imagen14.svg", alt='La imagen expone dos partes de una interfaz de software de diseo de patrones de costura. A la izquierda, se presenta la interfaz completa con una pieza de patrn curva (coloreada en azul) y un panel de propiedades con opciones como Principal, Nombre, Escalado, entre otras. A la derecha, representa una ampliacin de la misma pieza curva en azul, detallando los puntos y lneas de ajuste.')
        .row(titulo="15")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 15. ] Repita el mismo ejercicio en cada una de las bases con traslado de pinza para convertir a cortes, recuerde que los nombres comerciales de los cortes varan segn la regin o el pas donde se trabaje, siempre remtase a la parte funcional o tcnica de lo que se est trabajando.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta9/imagen15.svg", alt='La imagen presenta cuatro piezas de patrn de costura en una interfaz de software de diseo. Las piezas estn etiquetadas como Blusa CF, Blusa Espalda DEL, Blusa Espalda, y Blusa Espalda Fruncida (esta ltima coloreada en azul). Todas las piezas tienen lneas y puntos de ajuste, organizados de manera que parecen partes de un patrn para una blusa.')

