    Separador    
    .row.mb-4
      #t_1_6.titulo-segundo.color-primario
        h2 1.6  Blusa hasta la cadera - Optitex

    .row.justify-content-center.mb-5(data-aos="zoom-in-left")
      .col-sm-12.col-lg-9
        .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-5
          .bloque-texto-g__img(
            :style="{'background-image':`url(${require('@/assets/curso/temas/tema1/imagen2.svg')})`}"
          )
          .bloque-texto-g__texto.p-4
            p.mb-0 Para dar inicio al trazo de la blusa hasta cadera (pgina nmero 39 del manual) se trabajar con la base trazada anteriormente, con traslado de pinza por costado y pinza de ajuste. Este ejercicio brinda el contexto general para trabajar cualquier tipo de interpretacin o modificacin de prendas superiores.

    .tarjeta.tarjeta--azul.p-4.mb-4.borde-redondo(style="background-color: #F8F5FF")(data-aos="zoom-in-left")
      SlyderA(tipo="b")
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 1. ] Para modificar el largo de la prenda, en este caso hasta la altura de cadera; dirjase a #[b caja de herramientas], opcin #[b movimiento paralelo. ]
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen1.svg", alt='La imagen representa dos pantallazos de una interfaz de software de diseo de patrones de costura. En la parte izquierda, se presenta una pieza de patrn titulada Base Sup FRENTE (coloreada en azul) con lneas y puntos de ajuste, y un panel de herramientas a la izquierda con opciones como Mover punto, Movimiento paralelo, y Cortar/Borrar. A la derecha, se presenta una ampliacin de la misma pieza de patrn con ms detalle en las lneas y puntos de ajuste. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 2. ] Seleccionar la lnea de cintura en sentido de las manecillas del reloj. Cuando el cursor cambia de forma se puede alargar arrastrando el mouse.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen2.svg", alt='La imagen expone tres vistas de una pieza de patrn titulada Base Sup FRENTE (coloreada en azul) en una interfaz de software de diseo de patrones de costura. Las tres imgenes presentan la misma pieza, pero con diferentes lneas y puntos de ajuste, exponiendo variaciones en los detalles de costura y ajustes. Cada vista incluye lneas punteadas y slidas que indican diferentes secciones y posibles ajustes de la pieza del patrn. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 3. ] En la ventana emergente, #[b movimiento paralelo del tramo], marque el largo deseado, teniendo en cuenta el eje X y el eje Y, posteriormente haga clic en el botn #[b aceptar. ]
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen3.svg", alt='La imagen detalla dos partes de una interfaz de software de diseo de patrones de costura. A la izquierda, se presenta una ventana emergente titulada Movimiento paralelo del tramo, donde se pueden ingresar valores numricos y direcciones para ajustar el movimiento de los puntos del patrn. A la derecha, se expone una pieza de patrn titulada Base Sup FRENTE (coloreada en azul) con lneas y puntos de ajuste, indicando las modificaciones realizadas. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 4. ] Al modificar el largo se identifica la variacin en la pinza, es decir, se ve la pinza anclada al largo. Para eliminar la pinza, d #[b clic derecho] en la #[b herramienta seleccin], seleccione el trayecto o la pinza y suprimir, de igual manera, se debe suprimir todos y cada uno de los puntos de la pinza.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen4.svg", alt='La imagen detallan tres vistas de una pieza de patrn titulada Base Sup FRENTE (coloreada en azul) en una interfaz de software de diseo de patrones de costura. En la imagen izquierda, se expone un men desplegable con opciones como Seleccionar Herramienta, Lneas gua, Nueva pieza, Herramientas, Segmento, y Zoom. Las imagenes central y derecha presentan la misma pieza de patrn con diferentes detalles de lneas y puntos de ajuste, indicando posibles modificaciones en la costura. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 5. ] En este punto ya se tiene la blusa con el largo deseado, es necesario darle la forma de cada uno de los puntos del cuerpo. Para marcar el punto de cintura tome la herramienta puntos sobre y fijar un punto con atributo de #[b escalado], por costado con la medida de altura de cadera. Para verificar las medidas oprimir la #[b tecla F8. ]
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen5.svg", alt='La imagen expone dos partes de una interfaz de software de diseo de patrones de costura. A la izquierda, se detalla una ventana emergente titulada Atributos de Punto con opciones para ajustar el tipo de punto, su nmero, y sus coordenadas (X e Y), adems de opciones de movimiento y escalado. A la derecha, se detalla una pieza de patrn titulada Base Sup FRENTE (coloreada en azul) con lneas y puntos de ajuste, incluyendo medidas y coordenadas numricas para varios puntos del patrn. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 6. ] Verifique el recorrido de la lnea de cadera con la herramienta #[b medir. ]
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen6.svg", alt='La imagen presenta una interfaz de software de diseo de patrones de costura. A la izquierda, se expone una ventana emergente titulada Ventana de medidas con campos para ajustar la distancia, ngulo, y largo de un segmento, adems de opciones para editar el largo del segmento. A la derecha, se expone una pieza de patrn titulada Base Sup FRENTE (coloreada en azul) con lneas y puntos de ajuste. La ventana de medidas parece estar utilizada para modificar y precisar las dimensiones de la pieza del patrn. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 7. ] Aumente la medida de 1/4 del contorno de cadera ms pinza segn el manual, en editar segmento en la ventana de #[b medidas], asignar medida, y presione el botn #[b aceptar].
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen7.svg", alt='La imagen expone dos capturas de una interfaz de software de diseo de patrones de costura. Ambas capturas presentan la Ventana de medidas con campos para ajustar la distancia (Dist), ngulo (Angulo) y largo (Largo) de un segmento. En ambas ventanas, se estn modificando los valores del largo del segmento para la talla Base. A la derecha, se expone una pieza de patrn titulada Base Sup FRENTE (coloreada en azul) con lneas y puntos de ajuste. Las ventanas de medidas permiten modificar y precisar las dimensiones de esta pieza del patrn. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 8. ] Para marcar la pinza en segmento interno se debe marcar una lnea gua en cintura. Con la herramienta #[b puntos sobre], fije en el segmento de centro frente, un punto, en el punto de talle o cintura.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen8.svg", alt='La imagen detalla dos vistas de una interfaz de software de diseo de patrones de costura, ambas con la pieza de patrn titulada Base Sup FRENTE (coloreada en azul). A la izquierda, se presenta una ventana emergente titulada Atributos de Punto que permite ajustar las coordenadas X e Y, la distancia, y el ngulo de un punto especfico en el patrn, adems de opciones para mover el punto sobre el contorno y escalado. A la derecha, se detalla la misma pieza de patrn sin la ventana emergente, mostrando las lneas y puntos de ajuste. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 9. ] En el grupo de herramientas #[b insertar] identifique la herramienta #[b botn], y d clic en #[b crear botn]. Para marcar el punto de pinza se debe tener en cuenta el primer y segundo lado de pinza, el anlisis se hace tomando como referencia el ancho de pinza y la separacin de busto.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen9.svg", alt='La imagen detalla una interfaz de software de diseo de patrones de costura. Incluye una barra de herramientas con la opcin Crear Botn, una ventana emergente para ajustar las coordenadas y la posicin del botn, y una pieza de patrn Base Sup FRENTE con un botn agregado en el patrn. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 10. ] Crear cada uno de los puntos de pinza con la herramienta #[b botn], con el fin de poder marcar el ancho de la pinza.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen10.svg", alt='La imagen expone una interfaz de software de diseo de patrones de costura. A la izquierda, se presenta una barra de herramientas con opciones de edicin, y una pieza de patrn coloreada en azul con un smbolo de botn agregado. A la derecha, se presenta una pieza de patrn titulada Base Sup FRENTE con varios smbolos de botn agregados en diferentes puntos del patrn. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 11. ] Para marcar el largo de la pinza por la lnea de cadera utilizar la herramienta #[b punto.]
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen11.svg", alt='La imagen detalla una interfaz de software de diseo de patrones de costura con dos ventanas emergentes tituladas Atributos de Punto. Estas ventanas permiten ajustar las coordenadas, distancia, ngulo y proporciones de los puntos en el patrn. En el fondo, se exponen dos piezas de patrn coloreadas en azul con varios smbolos de botn agregados y lneas de ajuste visibles.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 12. ] Se debe identificar cada uno de los vrtices de pinza, teniendo en cuenta que no se puede encontrar el vrtice de pinza por costado y el vrtice de pinza de ajuste.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen12.svg", alt='La imagen expone dos patrones de costura para la parte delantera de una prenda. Ambos patrones tienen marcados los mismos puntos de referencia y lneas gua, pero el patrn de la izquierda incluye marcas adicionales para la colocacin de pinzas, mientras que el patrn de la derecha tiene una marca para un ojal. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 13. ] Para marcar o dibujar la pinza, en la herramienta #[b dibujar], opcin #[b borrador] se puede ir marcando cada uno de los puntos o vrtices sealados.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen13.svg", alt='La imagen describe dos patrones de costura adicionales para la parte delantera de una prenda. El patrn de la izquierda tiene una lnea verde que parece indicar una nueva lnea de corte o costura. El patrn de la derecha tiene un diseo con una pinza central que se extiende desde la parte inferior hasta el centro del patrn, adems de una marca para un ojal cerca de la parte superior.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.ps-5.mb-4 #[b 14. ] Para trazar la parte posterior de la blusa hasta la cadera se parte desde el bsico posterior. Trace una lnea gua en el punto de cintura costado, en este caso debe tener en cuenta los desniveles de cintura, es decir, el largo de la prenda se dar desde el punto de costado cintura, mientras que la pinza se marcar desde el punto de cintura centro atrs.
            p.ps-5 Para alinear los puntos de cintura, en el grupo de herramientas de #[b contorno] seleccione #[b alinear puntos] (tecla rpida G), seleccione los puntos de cintura que se quieren alinear; siempre se debe marcar en el sentido de las manecillas del reloj.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen14.svg", alt='La imagen detalla dos patrones de costura para la parte trasera de una prenda. Ambos patrones tienen marcas de referencia con medidas numricas en puntos clave. El patrn de la izquierda incluye una lnea vertical que puede indicar una costura o pliegue. El patrn de la derecha, que es una versin ms pequea del primero, tambin tiene una lnea vertical similar. Ambos patrones indican que son para la Base Sub ESPALDA y exponen opciones de alineacin en el software de diseo. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 15. ] En la ventana emergente seleccione #[b alinear al ltimo punto]. En este caso debe seleccionar #[b alinear todos los puntos. ]
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen15.svg", alt='La imagen describe una accin que es: en la ventana emergente, seleccione Alinear al ltimo punto. En esta ocasin, debe seleccionar Alinear todos los puntos.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 16. ] En la caja de herramientas escoja #[b movimiento paralelo], seleccione desde el punto de centro atrs hacia costado, en el sentido de las manecillas del reloj; extender hacia abajo, asignando la medida de altura de cadera, teniendo en cuenta el eje X y el eje Y.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen16.svg", alt='En la imagen se describe como en la caja de herramientas, se selecciona la opcin de movimiento paralelo. Luego, marque desde el punto central trasero hacia el costado, en sentido de las manecillas del reloj. Extienda hacia abajo, asignando la medida de la altura de la cadera, considerando los ejes X e Y. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 17. ] En este punto ya se tiene la blusa con el largo deseado, es necesario darle la forma de cada uno de los puntos del cuerpo. Para marcar el punto de cintura tome la herramienta #[b puntos sobre] y fije un punto con el atributo de #[b escalado], por costado con la medida de altura de cadera. Para verificar las medidas oprimir la #[b tecla F8. ]
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen17.svg", alt='La imagen detalla tres piezas de un patrn de costura, cada una correspondiente a una parte del cuerpo, probablemente para una prenda superior (como una blusa o chaqueta). Cada pieza tiene medidas especficas y puntos de referencia marcados, que ayudan en la confeccin y ensamblaje de la prenda.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 18. ] Verificar el recorrido de la lnea de cadera con la herramienta #[b medir]. Aumentar la medida de 1/4 del contorno de cadera ms pinza segn el manual; en editar segmento de la ventana de #[b medidas], asignar medida, y posteriormente d clic en el botn #[b aceptar. ]
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen18.svg", alt='La imagen detalla dos ventanas de un software de diseo de patrones, donde se ajustan las dimensiones y divisiones de segmentos del patrn, y una pieza de patrn de costura con medidas y puntos de referencia para confeccin de una prenda.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.ps-5.mt-5.mb-5 #[b 19. ] Para marcar la pinza en segmento interno se debe marcar una lnea gua en cintura. Con la herramienta #[b punto sobre], fije en el segmento de centro frente, un punto, en el punto de talle o cintura. En el grupo de herramientas #[b insertar ] identifique la herramienta #[b botn], haga clic en #[b crear botn. ] Para marcar el punto de pinza se debe tener en cuenta el primer y segundo lado de pinza, el anlisis se hace tomando como referencia el ancho de pinza y la separacin del busto.
            p.ps-5 Crear cada uno de los puntos de pinza con la herramienta botn, con el fin de poder marcar el ancho de la pinza. Para marcar el largo de la pinza por lnea de cadera utilizar la herramienta punto sobre. Se debe identificar cada uno de los vrtices de pinza, teniendo en cuenta que no se puede encontrar el vrtice de pinza por costado y el vrtice de pinza de ajuste. Para marcar o dibujar la pinza, en la herramienta dibujar, opcin borrador se puede ir marcando cada uno de los puntos o vrtices sealados.  
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen19.svg", alt='La imagen explica la accin para marcar la pinza en el segmento interno, trace una lnea gua en la cintura. Utilice la herramienta de punto sobre para fijar un punto en el segmento del centro frente, en el talle o cintura. En el grupo de herramientas de insertar, seleccione la herramienta botn y cree un botn. Para marcar el punto de pinza, considere los lados de la pinza y analice el ancho de la pinza y la separacin del busto. La imagen presenta una ventana del software de diseo de patrones, donde se pueden ajustar las coordenadas (X, Y), la distancia y el ngulo para la colocacin de un botn. A la derecha, se presenta un patrn de costura con una pinza marcada en el centro, indicando cmo se aplican las configuraciones del software en el diseo del patrn. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 20. ] Ya en este punto se identifica parte delantera y parte posterior.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen21.svg", alt='La imagen detalla dos piezas de un patrn de costura para la parte delantera y trasera de una prenda. Ambas piezas tienen pinzas marcadas para ajustar el contorno del busto y la cintura, con puntos de referencia y lneas gua que facilitan el ensamblaje y la confeccin.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 21. ] Para trazar la pechera o el cruce de botonadura se deben eliminar todos los puntos del centro frente. En el grupo de herramientas #[b editar ] escoja la herramienta #[b extender en paralelo ] y seleccione la #[b lnea centro frente ] en el sentido de las manecillas del reloj; en la ventana emergente asigne la medida del cruce de botonadura.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen22.svg", alt='La imagen expone la forma como Para trazar la pechera o el cruce de botonadura, elimine todos los puntos del centro frente. En el grupo de herramientas de edicin, seleccione la herramienta de extensin en paralelo y marque la lnea del centro frente en el sentido de las manecillas del reloj. En la ventana emergente, asigne la medida del cruce de botonadura.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 22. ] Al realizar la extensin utilice la herramienta crear un #[b contorno interno paralelo al tramo seleccionado], esta permite crear o extender el cruce de botonadura como se hace de manera manual sobre papel.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta10/imagen23.svg", alt='La imagen detalla la interfaz de un software de diseo de patrones, especficamente en la funcionalidad de crear una lnea paralela a un segmento. En la primera ventana emergente, se configuran la distancia y los puntos de extensin de la nueva lnea paralela. En la segunda ventana emergente, se pregunta si tambin se debe crear un punto sobre el contorno extendido. A la derecha, se expone una pieza de patrn con el centro frente y las pinzas marcadas.')

