    .tarjeta.tarjeta--gris.p-4.borde-redondo(style="background-color: #F8F5FF")(data-aos="zoom-in-left")
      LineaTiempoC.color-acento-botones(text-small)
        .row(titulo="1")
          .col-md-4.mb-4.mb-md-0
            p.ps-5.mb-4 #[b 1. ] Antes de iniciar el corte princesa y el corte francs se har el corte de almilla, trasladando la parte delantera hacia la parte posterior. A partir del punto cuello hombro y hombro sisa marque el recorrido de la almilla; una los dos puntos con la herramienta #[b dibujar]. Seleccionar la herramienta #[b cortar], opcin #[b cortar a pieza a lo largo de un interno. ]
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta11/imagen1.svg", alt='La imagen detalla dos piezas de patrn, una delantera y otra trasera, ambas con pinzas marcadas. En la parte superior, se expone una opcin del men del software de diseo de patrones, que permite Cortar la pieza a lo largo de un interno. Esta funcin facilita la modificacin del patrn siguiendo lneas internas definidas, como las de las pinzas o las lneas de ajuste.')
        .row(titulo="2")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 2. ] D clic y verifique que aparece un hilo de tela independiente sobre la pieza que se quiere cortar. De igual manera, se habilita la ventana emergente de costuras, si se desea se puede asignar el margen de costura o en este caso como se viene trabajando, no se asigna margen de costura.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta11/imagen2.svg", alt='La imagen expone una ventana de configuracin del software de diseo de patrones, titulada Smbolos de Costura. Aqu se pueden definir los tipos de lneas de costura, el final de las costuras y las cintas de sesgo. A la derecha, se presenta una pieza de patrn con ajustes en las pinzas y otros detalles. La ventana de configuracin permite personalizar los smbolos y las lneas que se utilizarn en el diseo del patrn para una mayor precisin en la confeccin. ')
        .row(titulo="3")
          .col-md-4.mb-4.mb-md-0
            p.ps-5.mb-4 #[b 3. ] Verificar que la pieza se puede trasladar o mover.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta11/imagen3.svg", alt='La imagen expone la forma como se puede  verificar que la pieza se puede trasladar o mover, para facilitar el proceso.')
        .row(titulo="4")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 4. ] Para poder unir la pieza al posterior se debe validar la medida de los escotes, dirjase al grupo de herramientas #[b insertar], opcin #[b voltear pieza] y seleccione #[b volteo horizontal. ]
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta11/imagen4.svg", alt='La imagen expone la forma como puede  unir la pieza al posterior, primero valide la medida de los escotes. Luego, vaya al grupo de herramientas Insertar, seleccione la opcin Voltear pieza y elija Volteo horizontal.')
        .row(titulo="5")
          .col-md-4.mb-4.mb-md-0
            p.ps-5.mb-4 #[b 5. ] Para que coincida hombro con hombro dirjase al grupo de herramientas #[b insertar], opcin #[b voltear pieza] y seleccione #[b volteo vertical. ]
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta11/imagen5.svg", alt='La imagen expone la forma para alinear los hombros, vaya al grupo de herramientas Insertar, seleccione la opcin Voltear pieza y elija Volteo vertical. ')
        .row(titulo="6")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 6. ] Para unir las piezas dirjase a la #[b herramienta piezas], opcin #[b recorrido]. Haga clic en la pieza que se cort el delantero y la parte posterior, y haga clic sobre la lnea de hombro para unir la pieza.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta11/imagen6.svg", alt='La imagen expone la forma como debe unir las piezas, vaya a la herramienta Piezas y seleccione la opcin Recorrido. Haga clic en la pieza cortada del delantero y en la parte posterior, luego haga clic en la lnea del hombro para unirlas. ')
        .row(titulo="7")
          .col-md-4.mb-4.mb-md-0
            p.ps-5.mb-4 #[b 7. ] Para fusionar las #[b piezas], en el grupo de herramientas piezas seleccione #[b unir piezas o unir partes. ]
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta11/imagen7.svg", alt='La imagen explica como se debe  unir las piezas, dirgete a la herramienta Unir piezas, selecciona la opcin Recorrido, haz clic en la pieza cortada del delantero y en la parte posterior, y luego haz clic en la lnea del hombro. En la ventana emergente Unir piezas, configura las opciones necesarias y haz clic en OK. ')
        .row(titulo="8")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 8. ] Luego de fusionadas las piezas si no se identifican curvas suaves en cada unin suavice los puntos; tome la herramienta #[b seleccin], haga clic sobre el punto y asigne atributo #[b curva]. Con la herramienta #[b mover punto] deslice el mouse hasta suavizar la curva. De igual manera, suavice el punto de hombro escote.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta11/imagen8.svg", alt='La imagen describe como una vez fusionadas las piezas, si no se presentan curvas suaves en cada unin, suavice los puntos. Utilice la herramienta de seleccin, haga clic sobre el punto y asigne el atributo de curva. Luego, con la herramienta de mover puntos, deslice el mouse hasta suavizar la curva. Repita el proceso para suavizar el punto en el hombro y el escote. ')
        .row(titulo="9")
          .col-md-4.mb-4.mb-md-0
            p.ps-5.mb-4 #[b 9. ] Ya en este punto se identifica la correccin de las uniones en cada uno de los puntos de hombro. Si el hilo de tela que se identifica en la pieza no corresponde al hilo de tela del trazo o corte proceda a modificarlo con la opcin #[b mover interno ] (tecla rpida I). Arrastre o mueva el hilo de tela de la pieza delantera, haciendo interseccin sobre el hilo de la tela posterior, haga clic sobre este; se presenta que el hilo de tela se modifica quedando paralelo al centro atrs.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta11/imagen9.svg", alt='La imagen expone tres vistas de un patrn de costura en color azul, con lneas y puntos de control que delinean su forma. Cada vista parece representar diferentes ngulos o ajustes del mismo patrn, posiblemente relacionados con las modificaciones en la estructura del diseo de una prenda. Las lneas negras y los puntos de control rosa indican las costuras y ajustes precisos en el diseo del patrn, mientras que las flechas y medidas sugieren direcciones y longitudes especficas para el corte y confeccin. ')
        .row(titulo="10")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 10. ] Lo ideal es mover los hilos de tela en tal punto que no interfieran con los cortes que se van a trabajar. Para realizar los cortes, en el grupo de herramientas #[b pieza] seleccione #[b cortar], opcin #[b cortar pieza.] Haga clic sobre el punto de hombro, y en la ventana emergente, en la opcin #[b proporcional], asignar 0.5 para que quede en la mitad.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta11/imagen10.svg", alt='La imagen expone una interfaz de software de diseo de patrones, donde se detalla la opcin de Cortar a pica en el men superior. Adems, se presenta un cuadro de dilogo titulado Atributos de Punto, donde se pueden ajustar diversos parmetros como el tipo de punto, las coordenadas X e Y, la distancia, y el ngulo. Estas herramientas permiten realizar ajustes precisos en el diseo del patrn, facilitando la creacin y modificacin de puntos clave en la pieza. ')
        .row(titulo="11")
          .col-md-4.mb-4.mb-md-0
            p.ps-5.mb-4 #[b 11. ] Vaya al vrtice de pinza, posteriormente al primer lado de pinza por cintura, y finalice en el primer lado de pinza por largo total. En la ventana emergente de #[b costura] haga clic en el botn #[b cancelar], identifique el traslado o movimiento del corte.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta11/imagen11.svg", alt='Para suavizar las uniones, use la herramienta de seleccin para hacer clic en los puntos de las curvas y asigne el atributo de curva. Luego, con la herramienta de mover puntos, deslice el mouse hasta suavizar las curvas en las uniones, incluyendo el punto del hombro y escote. ')
        .row(titulo="12")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 12. ] Repita el ejercicio hasta generar el corte de costado. Identifique cada una de las piezas, centro, frente, costado y pinza.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta11/imagen12.svg", alt='La imagen describe como suavizar las uniones, para ello utilice la herramienta de seleccin para hacer clic en los puntos de las curvas y asigne el atributo de curva. Luego, con la herramienta de mover puntos, deslice el mouse hasta suavizar las curvas en las uniones, incluyendo los puntos en los hombros y escotes. ')
        .row(titulo="13")
          .col-md-4.mb-4.mb-md-0
            p.ps-5.mb-4 #[b 13. ] En la pieza de costado identifique la pinza de recuperacin de talle, para cerrar la pinza, en el men herramientas, opcin #[b escalado], seleccione #[b cerrar pinza]. En la ventana emergente #[b fijar pinza] seleccione #[b intermedia entre primer y segundo segmento de pinza.]
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta11/imagen13.svg", alt='La imagen describe como suavizar las uniones, para ello utilice la herramienta de seleccin para hacer clic en los puntos de las curvas y asigne el atributo de curva. Luego, con la herramienta de mover puntos, deslice el mouse hasta suavizar las curvas en las uniones, incluyendo los puntos en los hombros y escotes. ')
        .row(titulo="14")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 14. ] Suavice los vrtices de pinza en cada uno de los cortes.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta11/imagen14.svg", alt='La imagen describe dos patrones de costura para una prenda de vestir, probablemente una blusa o una camisa. Cada patrn est coloreado en azul claro y tiene lneas y puntos que indican las lneas de corte y costura. La pieza de la izquierda parece ser la parte delantera del patrn, y la pieza de la derecha, la parte trasera. Ambos patrones tienen flechas y textos que posiblemente indican instrucciones de ensamblaje o alineacin.')
        .row(titulo="15")
          .col-md-4.mb-4.mb-md-0
            p.ps-5.mb-4 #[b 15. ] Repita los mismos pasos en la parte posterior para generar el corte princesa en la espalda.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta11/imagen15.svg", alt='La imagen detalla cuatro patrones de costura para una prenda, con etiquetas y flechas que indican instrucciones de ensamblaje. La cuarta pieza est coloreada en azul claro.')
        .row(titulo="16")
          .col-md-4.mb-4.mb-md-0
            p.ps-5 #[b 16. ] Para generar el corte francs en cualquiera de las dos piezas repita los pasos anteriores, partiendo desde sisa, que es donde sale el corte francs.
          .col-md-8
            img.p-2(src="@/assets/curso/temas/tema1/tarjeta11/imagen16.svg", alt='La imagen detalla dos patrones de costura, uno de los cuales est coloreado en azul claro. Las piezas tienen etiquetas y flechas que indican instrucciones de ensamblaje. ')

