    .row.mb-4
      #t_1_8.titulo-segundo.color-primario
        h2 1.8  Modelos de faldas - Optitex

    .tarjeta.tarjeta--azul.p-4.mb-4.borde-redondo(style="background-color: #F8F5FF")(data-aos="zoom-in-left")
      SlyderA(tipo="b")
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 1. ] Para el trazo de tres modelos de falda: falda media campana (pgina 36), falda con vuelo (pgina 37) y falda sirena revise en el #[b manual de patronaje SENA (pgina 34). ]
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta12/imagen1.svg", alt='La imagen describe una captura de pantalla de un software de diseo de patrones de costura. En la parte superior hay un men desplegable con opciones relacionadas con pliegues y pinzas. Debajo del men, se presente un patrn de costura coloreado en azul claro, con una flecha y etiquetas que indican instrucciones de ensamblaje.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 2. ] Se trabajar la falda de dos pinzas para generar el vuelo hacia el ruedo, cree el primer traslado, tome la herramienta #[b pinza], opcin #[b aadir y girar pinza. ]
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta12/imagen2.svg", alt='La imagen describe tres patrones de costura, todos coloreados en azul claro y etiquetados con instrucciones y flechas para el ensamblaje. Adems, hay una ventana emergente de Atributos de Punto que parece ser parte del software de diseo de patrones, detalla las opciones para ajustar los detalles de las costuras y las pinzas. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 3. ] Acrquese con el puntero al vrtice de pinza, una vez cambie de color d clic para seleccionarla y #[b arrastre] hasta el ruedo, en el recuadro #[b atributos de punto] proporcione la ubicacin deseada del punto al cual se requiera trasladar la pinza.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta12/imagen3.svg", alt='La imagen expone tres patrones de costura, todos coloreados en azul claro y con etiquetas y flechas que indican instrucciones de ensamblaje. El primer patrn tiene lneas de pinzas en la parte superior. El segundo patrn incluye una lnea verde adicional que podra representar una modificacin o ajuste. El tercer patrn indica una lnea discontinua y una lnea verde que indica un posible cambio en el diseo o la posicin de alguna parte del patrn. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 4. ] Lleve el puntero a cualquier parte del costado de la falda, haga clic, gire, haga clic y traslade la pinza.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta12/imagen4.svg", alt='La imagen expone cinco patrones de costura, todos coloreados en azul claro y con etiquetas y flechas que indican instrucciones de ensamblaje. Algunos patrones tienen lneas discontinuas y verdes que indican modificaciones o ajustes en el diseo. En la parte superior central, hay una ventana emergente de Atributos de Punto que forma parte del software de diseo, mostrando opciones para ajustar los detalles de las costuras y las pinzas. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 5. ] Se realiza el mismo procedimiento trasladando la otra pinza, una vez cambie de color el vrtice de pinza, haga clic para seleccionar y arrastre hasta el ruedo dando clic sobre este, en el recuadro #[b atributos de punto ] indique la #[b ubicacin deseada], haga clic en cualquier parte del costado, gire, haga clic y traslade pinza.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta12/imagen5.svg", alt='La imagen expone cuatro patrones de costura, todos coloreados en azul claro, con etiquetas y flechas que indican instrucciones de ensamblaje. Algunos patrones tienen lneas discontinuas, probablemente para indicar ajustes o pliegues. A la izquierda de la imagen, hay un men desplegable del software de diseo con varias opciones de herramientas para trabajar con los patrones. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 6. ] Haga clic derecho en la herramienta #[b seleccin], elija la pinza a eliminar y suprima cada una de las lneas para limpiar la pieza.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta12/imagen6.svg", alt='La imagen expone dos patrones de costura, ambos coloreados en azul claro y con lneas y puntos que indican detalles de ensamblaje. El patrn de la izquierda tiene una flecha y una lnea que podra representar una direccin del grano o una pinza. El patrn de la derecha parece ser una representacin ampliada de una seccin de un patrn, exponiendo ms detalles en los puntos de ajuste o costura. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 7. ] Para suavizar los puntos que se generaron por los traslados de pinza seleccione los puntos en el sentido de las manecillas del reloj, haga clic en el ltimo punto.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta12/imagen7.svg", alt='La imagen presenta tres paneles de propiedades del software de diseo de patrones, todos titulados Propiedades. Cada panel tiene varias opciones ajustables como Escalado, Curva, Conectado, Nombre, ngulo, Costura y Punto alternativo de partida. Las casillas de verificacin permiten al usuario seleccionar o deseleccionar estas opciones para ajustar las caractersticas del patrn. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 8. ] En la pestaa #[b propiedades] se identifica que los puntos tienen atributo de #[b escalado], desmrquelo y seale de #[b curva.]
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta12/imagen8.svg", alt='La imagen detalla cuatro patrones de costura, todos coloreados en azul claro y con lneas y puntos que indican detalles de ensamblaje. Tres de los patrones parecen ser secciones ampliadas, mostrando ms detalles en los puntos de ajuste o costura. Uno de los patrones (izquierda) tiene una lnea que podra representar una direccin del grano o una pinza. Los otros patrones describen curvas y puntos de referencia para el ensamblaje. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 9. ] Haga clic en los puntos que no se requieren, y luego suprimir.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta12/imagen9.svg", alt='La imagen detalla dos patrones de costura coloreados en azul claro. El patrn de la izquierda tiene una flecha y lneas que indican instrucciones de ensamblaje, probablemente para la direccin del grano o una pinza. El patrn de la derecha parece ser una seccin ms grande, tambin con una flecha similar. Ambas piezas tienen lneas punteadas en la parte inferior que podran indicar un borde o una lnea de corte adicional. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 10. ] Con ayuda de una lnea gua ubique el ruedo de la falda centro frente y verifique que la curva no sobrepase la lnea escuadrada.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta12/imagen10.svg", alt='La imagen detalla varios patrones de costura, todos coloreados en azul claro y con lneas y puntos que indican detalles de ensamblaje. Hay un panel de Propiedades en el centro que permite ajustar caractersticas como Escalado, Curva y Conectado. Los patrones incluyen flechas y lneas que indican la direccin del grano o las pinzas, y algunos tienen lneas punteadas que podran representar bordes o lneas de corte adicionales. ')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 11. ] Haga clic en los puntos de la cintura en sentido de las manecillas del reloj, en la pestaa #[b propiedades ] quite el atributo de #[b escalado] y asigne el atributo de #[b curva], suprima uno de los dos puntos y verifique que la lnea gua de la curva no sobrepase la lnea escuadrada.
          .col-md-8
            img.p-3(src="@/assets/curso/temas/tema1/tarjeta12/imagen11.svg", alt='La imagen expone varios patrones de costura, algunos coloreados en azul claro y otros sin color, con etiquetas y flechas que indican instrucciones de ensamblaje. En la parte superior hay una ventana del software titulada Ventana piezas que detalla las diferentes piezas de una falda. Los patrones incluyen lneas y flechas que indican la direccin del grano y posibles pinzas o pliegues, con lneas punteadas que podran representar bordes o lneas de corte adicionales.')
        .row.justify-content-center.my-lg-4
          .col-md-4.mb-4.mb-md-0
            p.p-5 #[b 12. ] Seleccione en la ventana piezas otra falda; para ajustar el hilo de tela seleccione la herramienta #[b copiar o mover interno], arrastre al otro hilo de tela y haga clic.
          .col-md-8
